@import 'aggregations.css';
@import 'combined-email.css';
@import 'dynamic-checklist.css';
@import 'electronic-signature.css';
@import 'formula-fields.css';
@import 'layout-manager.css';
@import 'partitioned-view.css';
@import 'tabs.css';
@import 'custom-filters.css';
@import 'shepherd.css';

/**
 * EMAIL COUNT
 */
#email-count-badge {
	position: absolute;
	bottom: 5px;
	left: 16px;
	z-index: 10;
	width: 26px;
	text-align: center;
	overflow: hidden;
}

#email-count-badge .badge {
	font-size: 9px;
	padding: 2px 5px;
	background-color: var(--brand-warning);
	color: var(--white-color);
	display: inline-block;
}

/* color picker */
.colorpicker-selectors i {
	width: 20px;
	height: 20px;
	display: inline-block;
	float: none;
	border: 1px solid #555555;
}

.colorpicker-selectors {
	width: 138px;
	text-align: center;
	height: auto;
}

/* record list */
table.table > tbody td[data-name='remove-cross'] > .list-row-buttons button,
table.table > tbody td[data-name='sort-magnet'] > .list-row-buttons button {
	padding: 0;
}

.list.edit-mode > table td {
	overflow: visible;
}

.link-multiple-inline-edit {
	position: absolute;
	top: 0;
	right: 8px;
}

/** ares field **/
.sic-code-control .sic-fill-input {
	width: calc(100% - 40px);
	display: inline-block;
}

.sic-code-control .btn-fill-data {
	float: right;
}

.sic-code-control {
	clear: both;
}

.sic-fill-dropdown {
	display: inline-block;
	position: relative;
}

/** easy email overlay (edit html) **/
body .arco-drawer-wrapper {
	z-index: 9999;
	/* > espo navbar */
}

/* quick view */

.quick-view-context-menu {
	cursor: help;
}

.cell.horizontal-label {
	display: flex;
	align-items: center;
}

.cell.horizontal-label .control-label {
	margin-right: 10px;
	flex-shrink: 0;
}

.cell.horizontal-label .field {
	flex-grow: 1;
}

.deadline-date {
	color: var(--brand-danger);
}

.deadline-date-day-before {
	color: var(--brand-warning);
}

.kanban-switcher {
	display: flex;
	justify-content: flex-end;
}
