.email-list-container .list-row > .cell[data-name='combinedCell'] {
	cursor: pointer;
}

.email-list-container .list-row > .cell[data-name='combinedCell'] .subject-container {
	overflow: hidden;
	text-overflow: ellipsis;
}

.email-list-container .list {
	margin-right: 0 !important;
}

.email-list-container tr.selected > td {
	background-color: var(--state-success-bg);
}

.email-combined.is-combined .email-list-container table {
	max-height: calc(100vh - 230px);
	display: flex;
	width: 100%;
	flex-direction: column;
}

.email-combined.is-combined .email-list-container table thead {
	flex: 0 0 auto;
}

.email-combined.is-combined .email-list-container table tbody {
	flex: 1 1 auto;
	display: block;
	overflow-y: auto;
}

.email-combined.is-combined .email-list-container table tbody td[data-name='r-checkbox'] {
	width: 40px;
}

.email-combined.is-combined .email-list-container table tbody::-webkit-scrollbar {
	width: 8px;
}

.email-combined.is-combined .email-list-container table thead,
.email-combined.is-combined .email-list-container table tbody tr {
	display: table;
	table-layout: fixed;
	width: 100%;
}

.email-combined.is-combined .detail-container .panel > .panel-body {
	max-height: 800px;
	overflow-y: auto;
}

.email-combined.is-combined .detail-container .panel > .panel-body::-webkit-scrollbar {
	width: 8px;
}

.email-list-container .list-row .top > div {
	display: inline-block;
}

.email-list-container .list-row .date-container {
	float: right;
}

/* show the hover actions only to devices that support hover */
@media (hover: hover) {
	.email-list-container .list-row:hover .date-container {
		display: none;
	}

	.email-list-container .list-row:hover .hover-actions {
		display: table-cell;
	}
}

.email-list-container .list-row .hover-actions {
	display: none; /* hide by default */
	cursor: default; /* overriding pointer from hovering over other rows */
	padding: 0 !important;
	vertical-align: middle;
	text-align: center;
	width: 100px;
}

.email-list-container .list-row .hover-actions .container {
	display: flex;
	height: 100%;
	flex-direction: row;
	align-items: center;
	justify-content: center;
}

.email-list-container .list-row .hover-actions i {
	padding: 0.4em;
	cursor: pointer;
	border-radius: 50%;
}

.email-list-container .list-row .hover-actions i:hover {
	background-color: var(--table-bg);
}

.email-list-container .list-row .hover-actions .read.active {
	color: yellowgreen;
}

.email-list-container .list-row .hover-actions .important.active {
	color: orange;
}

.email-list-container .list-row .hover-actions .trash.active {
	color: crimson;
}

.email-list-container.sticked {
	position: fixed;
	top: 75px;
}
